	background: radial-gradient(circle at 30% 30%, rgba(0, 255, 255, 0.07), transparent 60%),
		radial-gradient(circle at 70% 70%, rgba(255, 0, 255, 0.07), transparent 60%),
		#131319;
	color: white;
	display: flex;
	flex-direction: column;
	height: 100vh;
	background-attachment: fixed;
}

.body-container {
	flex: 1;
}

h2 {
	padding: 20px;
	font-size: 2rem;
}

h3 {
	font-size: 32px;
	text-align: center;
}

.location-div {
	margin-top: 60px;
	padding: 100px 100px;
	display: flex;
	gap: 5px;
	flex-direction: column;
	margin-left: auto;
	margin-right: auto;
	width: max-content;
	/* border: 2px solid red; */
}

form {
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.search-btn {
	width: 100px;
	margin-left: auto;
	margin-right: auto;
	font-size: 1.1rem;
	padding: 10px 5px;
	border-radius: 10px;
	background-color: transparent;
	color: white;
	border: 1px solid white;
}

input {
	height: 50px;
	width: 400px;
	border-radius: 10px;
	border: none;
	background-color: #1E1E29;
}

footer {
	padding: 5px;
	text-align: center;
}

/* Weather details page */

.main-container {
	height: 85vh;
	width: 80vw;
	margin: auto;
	display: flex;
	gap: 10px;
	flex-wrap: wrap;
}

/* Left div */

.search-bar-form {
	display: flex;
	flex-direction: row;
}

.search-bar-form>input {
	flex: 1;
}

.search-icon {
	height: 30px;
	margin: auto;
}

.weather-div {
	flex: 1;
	display: flex;
	flex-direction: column;
	gap: 10px;
	padding: 10px;
	border-radius: 10px;
	background-color: #16161F;
}

.search-bar {
	border-radius: 10px;
	box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
	transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.search-bar input:hover {
	transform: scale(1.01);
}

.search-bar input {
	transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.search-bar img {
	transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.search-bar img:hover {
	transform: scale(1.1);
}

.day-weather {
	border-radius: 10px;
	flex: 1;
	background-size: contain;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
	transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.day-weather:hover {
	transform: scale(1.01);
}

/* left-div upper-weather */

.upper-weather {
	display: flex;
	justify-content: space-between;
	padding: 30px;
	font-weight: 600;
}

.upper-weather .location {
	font-size: 1.7rem;
	font-weight: 700;
}

.upper-weather .time {
	font-size: 1.3rem;
}

/* left-div - lower-weather */


.lower-weather {
	display: flex;
	justify-content: space-between;
	padding: 30px;
	padding-right: 0px;
	padding-bottom: 0px;
}

.lower-left {
	padding: 0;
}

.lower-left .temp {
	font-weight: 900;
	font-size: 5rem;
}

.lower-left .temp-minmax {
	font-size: 1.5rem;
	font-weight: 700;
}

.lower-left .weather-desc {
	font-size: 1.5rem;
	font-weight: 700;
}

/* Right Div */

.details {
	display: flex;
	flex-direction: column;
	gap: 10px;
	flex: 1;
	border-radius: 10px;
	color: rgb(191, 191, 212);
}

.weather-details {
	background-color: #16161F;
	flex: 3;
	padding: 20px;
	border-radius: 10px;
	display: flex;
	flex-direction: column;
}

.week-forecast {
	background-color: #16161F;
	flex: 2;
	padding: 20px;
	border-radius: 10px;
}

/* Right top div */

.weather-details>p {
	font-size: 1.2rem;
	color: rgb(127, 127, 152);
	font-weight: 700;
}

.today-weather-details {
	display: flex;
	flex-direction: column;
	gap: 10px;
	flex: 1;
}

.today-weather-details>div {
	display: grid;
	align-items: center;
	grid-template-columns: 1fr 10fr 1.4fr;
	flex-grow: 1;
	border-bottom: 1px solid rgb(30, 30, 41);
	transition: transform 0.2s ease;
}

.today-weather-details>div:hover {
	transform: scale(1.005);
}

.today-weather-details>div>img {
	height: 32px;
	width: 32px;
	opacity: 0.3;
}

/* right bottom */

.week-days {
	color: rgb(191, 191, 212);
	display: flex;
	gap: 10px;
	text-align: center;
}

.week-days>div {
	padding: 5px;
	margin-top: 20px;
	transition: transform 0.3s ease;
}

.week-days>div:hover {
	transform: scale(1.05);
}

.week-icon {
	background-size: contain;
}

.week-forecast>p {
	color: rgb(127, 127, 152);
	font-weight: 700;
	font-size: 1.2rem;
}

.today-value {
	color: white;
	font-weight: 700;
}

.today-property {
	font-weight: 600;
}`, "",{"version":3,"sources":["webpack://./src/dom/dom.css"],"names":[],"mappings":"AAAA;CACC;;SAEQ;CACR,YAAY;CACZ,aAAa;CACb,sBAAsB;CACtB,aAAa;CACb,4BAA4B;AAC7B;;AAEA;CACC,OAAO;AACR;;AAEA;CACC,aAAa;CACb,eAAe;AAChB;;AAEA;CACC,eAAe;CACf,kBAAkB;AACnB;;AAEA;CACC,gBAAgB;CAChB,oBAAoB;CACpB,aAAa;CACb,QAAQ;CACR,sBAAsB;CACtB,iBAAiB;CACjB,kBAAkB;CAClB,kBAAkB;CAClB,2BAA2B;AAC5B;;AAEA;CACC,aAAa;CACb,sBAAsB;CACtB,SAAS;AACV;;AAEA;CACC,YAAY;CACZ,iBAAiB;CACjB,kBAAkB;CAClB,iBAAiB;CACjB,iBAAiB;CACjB,mBAAmB;CACnB,6BAA6B;CAC7B,YAAY;CACZ,uBAAuB;AACxB;;AAEA;CACC,YAAY;CACZ,YAAY;CACZ,mBAAmB;CACnB,YAAY;CACZ,yBAAyB;AAC1B;;AAEA;CACC,YAAY;CACZ,kBAAkB;AACnB;;AAEA,yBAAyB;;AAEzB;CACC,YAAY;CACZ,WAAW;CACX,YAAY;CACZ,aAAa;CACb,SAAS;CACT,eAAe;AAChB;;AAEA,aAAa;;AAEb;CACC,aAAa;CACb,mBAAmB;AACpB;;AAEA;CACC,OAAO;AACR;;AAEA;CACC,YAAY;CACZ,YAAY;AACb;;AAEA;CACC,OAAO;CACP,aAAa;CACb,sBAAsB;CACtB,SAAS;CACT,aAAa;CACb,mBAAmB;CACnB,yBAAyB;AAC1B;;AAEA;CACC,mBAAmB;CACnB,yCAAyC;CACzC,qDAAqD;AACtD;;AAEA;CACC,sBAAsB;AACvB;;AAEA;CACC,qDAAqD;AACtD;;AAEA;CACC,qDAAqD;AACtD;;AAEA;CACC,qBAAqB;AACtB;;AAEA;CACC,mBAAmB;CACnB,OAAO;CACP,wBAAwB;CACxB,aAAa;CACb,sBAAsB;CACtB,8BAA8B;CAC9B,yCAAyC;CACzC,qDAAqD;AACtD;;AAEA;CACC,sBAAsB;AACvB;;AAEA,2BAA2B;;AAE3B;CACC,aAAa;CACb,8BAA8B;CAC9B,aAAa;CACb,gBAAgB;AACjB;;AAEA;CACC,iBAAiB;CACjB,gBAAgB;AACjB;;AAEA;CACC,iBAAiB;AAClB;;AAEA,6BAA6B;;;AAG7B;CACC,aAAa;CACb,8BAA8B;CAC9B,aAAa;CACb,kBAAkB;CAClB,mBAAmB;AACpB;;AAEA;CACC,UAAU;AACX;;AAEA;CACC,gBAAgB;CAChB,eAAe;AAChB;;AAEA;CACC,iBAAiB;CACjB,gBAAgB;AACjB;;AAEA;CACC,iBAAiB;CACjB,gBAAgB;AACjB;;AAEA,cAAc;;AAEd;CACC,aAAa;CACb,sBAAsB;CACtB,SAAS;CACT,OAAO;CACP,mBAAmB;CACnB,yBAAyB;AAC1B;;AAEA;CACC,yBAAyB;CACzB,OAAO;CACP,aAAa;CACb,mBAAmB;CACnB,aAAa;CACb,sBAAsB;AACvB;;AAEA;CACC,yBAAyB;CACzB,OAAO;CACP,aAAa;CACb,mBAAmB;AACpB;;AAEA,kBAAkB;;AAElB;CACC,iBAAiB;CACjB,yBAAyB;CACzB,gBAAgB;AACjB;;AAEA;CACC,aAAa;CACb,sBAAsB;CACtB,SAAS;CACT,OAAO;AACR;;AAEA;CACC,aAAa;CACb,mBAAmB;CACnB,qCAAqC;CACrC,YAAY;CACZ,wCAAwC;CACxC,+BAA+B;AAChC;;AAEA;CACC,uBAAuB;AACxB;;AAEA;CACC